// import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faFacebook, faInstagram, faTwitter, faLinkedin } from '@fortawesome/free-brands-svg-icons';
// import './SocialMediaIcons.css';

export const Link1 = () => {
  const social ={
    display: 'flex',
    justifyContent: 'space-around',
    alignItems: 'center',
    width: '200px', /* Adjust the width as needed */
    margin: '16px auto', /* Adjust margin as needed */
  }
  
  const icon= {
    fontSize: '24px', /* Adjust the font size as needed */
    color: '#555', /* Adjust the icon color as needed */
    cursor: 'pointer',
  }
  
  // .icon:hover {
  //   color: #007bff; /* Adjust the hover color as needed */
  // }
  return (
    <div style={{social}}>
      <FontAwesomeIcon style={{icon}} icon={faFacebook} />
      <FontAwesomeIcon style={{icon}} icon={faInstagram} />
      <FontAwesomeIcon style={{icon}} icon={faTwitter} />
      <FontAwesomeIcon style={{icon}} icon={faLinkedin} />
    </div>
  );
};

