import { Grid } from '@mui/material'
import React from 'react'
import { Link1 } from './Link1'
import { Link2 } from './Link2'
import { Link3 } from './Link3'

export const Footer = () => {
  
  return (
    <div style={{position: 'absolute',
      bottom: '0',}}>
      <Grid container spacing={2}>
  <Grid item xs={3}>
    <Link1/>
  </Grid>
  <Grid item xs={6}>
    <Link2/>
  </Grid>
  <Grid item xs={3}>
    <Link3/>
  </Grid>
  
</Grid>
    <footer>
      uibninini
    </footer>
    </div>
  )
}
