import React from 'react'
import AppStoreImage from '../images/app-store-logo.png';
import GooglePlayImage from '../images/playstore.jpg';

export const Link3 = () => {
  return (
    <div  >
      <a href="https://apps.apple.com/" >
        <img style={{width:'200px',height:'100px'}} src={AppStoreImage} alt="Download on the App Store" />
      </a>
      <a href="https://play.google.com/" target="_blank" rel="noopener noreferrer">
        <img style={{width:'200px',height:'100px'}} src={GooglePlayImage} alt="Get it on Google Play" />
      </a>
    </div>
  )
}
