import React from 'react'
import ALogo from '../main1/ALogo.jpg'
export const Head = () => {
    const sty={  
        display: 'inline-block',
        float:'left',
        height:'80px', 
        padding:'10px 0 10px 300px', 
    };
    const container={
        color:'white',
        margin:'20px 0',
        width:'100%',
        height:'100px',
        borderRadius:'100px',
        backgroundColor: "black",
        
    };
    const textStyle={
        textAlign:'center',
        // height:'10px',
        // width:'80%',
    };
  return (
    <div style={container}>
        <img style={sty} src={ALogo} alt='A+'/>
        <h4 style={textStyle}>GATE RESULTS</h4>
    </div>
  )
}
